body {
  background: #123;
  color: #FED;
  padding: 0;
  margin: 0;
}

main {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-flow: column;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  p { text-align: center;}
  background-size: cover;
  min-height: 100vh;

  .hover-title {
    position: absolute;
    z-index: 100;
    width: 100%;
    text-align: center;
    background-color: rgba(0,0,0,.3);
    top: -1em;
    left: 0;
    pointer-events: none;
    user-select: none;
    padding: 1em;
    font-size: 1.2em;
  }

  .logo {
    width: 20vw;
    height: 15%;
    min-width: 86px;
    margin: 20px;
    z-index: 1000;
    pointer-events: none;
  }

  .oe-logo {
    width: 6.88vw * 3;
    height: 1.8vw * 3;
    min-width: 86px;
    min-height: 22.5px;
    max-width: 688px / 4;
    max-height: 180px / 4;
    position: fixed;
    bottom: 1em;
    right: 1em;
  }
  

  .welcome-message {
    width: 22em;
    font-size: .85vw;
    color: rgb(92, 167, 238);
    padding: 2em;
    align-self: flex-start;
    position: relative;
    user-select: none;

    p { width: 100%; text-align: left;}
  }
}

.debugger {
  position: absolute;
  display: flex;
  justify-items: center;
  align-items: center;
  align-content: center;
  top: 0;
  left: 0;
  width: auto;
  height: 3em;
  background: #34A;
  pointer-events: none;
  user-select: none;
  transform: translate(-30px, -40px);
  z-index: 50;
  border-radius: .3em;
  
  span {
    padding: 0 1em;
    width: 100%;
    text-align: center;
  }
}

.no-pointer { pointer-events: none; }

.full-screen {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 1;
}

circle {
  cursor: move;
}

.base-color { fill: royalblue;}
._23A {fill: rgb(17, 74, 128);}
._765 {fill: rgb(121, 64, 0);}
._A75 {fill: rgb(37, 47, 70);}
._A34 {fill: rgb(165, 13, 84);}
._782 {fill: rgb(6, 76, 78);}
._11F {fill: rgb(117, 15, 88);}
._927 {fill: rgb(48, 131, 76);}
._72A {fill: rgb(76, 18, 110);}
._FED {fill: rgb(214, 83, 7);}
._123 {fill: rgb(78, 13, 46);}
._888 {fill: rgb(42, 51, 3);}

.off {display: none;}

[data-opened="true"] {
  stroke-dasharray: 2,3;
}